import * as React from 'react';

export type Props = {
	index: number;
	style?: object;
	className?: string;
	header: React.ReactNode;
	content: React.ReactNode;
	expanded: boolean;
	onClick: Function;
	showCaret?: boolean;
	caretElement?: JSX.Element;
};

const CARET_RIGHT = '\u25B8';
const CollapsibleRow = (props: Props) => {
	const {
		index,
		style = {},
		className = '',
		header = null,
		content = null,
		onClick = () => {},
		expanded = false,
		showCaret = true,
		caretElement = null
	} = props;
	const providedClassName = className ? className : '';
	const expandedClassName = expanded ? 'expanded' : '';
	const _className = `collapsible-row ${providedClassName} ${expandedClassName}`;

	return (
		<div key={index} className={_className} style={style}>
			<div className="collapsible-row-header" onClick={() => onClick(index)}>
				{showCaret && (
					<span className={`row-caret ${expanded ? 'expanded' : ''}`}>
						{caretElement ? caretElement : CARET_RIGHT}
					</span>
				)}
				{header}
			</div>
			{expanded && <div className="collapsible-row-content">{content}</div>}
		</div>
	);
};

export default CollapsibleRow;
