import * as React from 'react';
import * as ReactDOM from 'react-dom';

import {
	Table,
	Column,
	AutoSizer,
	IndexRange,
	OverscanIndexRange,
	TableRowProps,
	Index
} from 'react-virtualized';
import 'react-virtualized/styles.css';
import './style.css';
import CollapsibleRow from './CollapsibleRow';
import withInfiniteLoader from './withInfiniteLoader';

export type TableProps = {
	data: Array<any>;
	height: number;
	headerHeight: number;
	columns: Array<object>;
	rowHeight: Function;
	rowRenderer: Function;
	extraContentRenderer: Function;
	maxRowCount?: number;
	rowClassName?: string;
	showCaret?: boolean;
	caretElement?: JSX.Element;
	isRowLoaded?: (index: Index) => Boolean;
	noRowsRenderer?: () => JSX.Element;
	onRowsRendered?: (info: IndexRange & OverscanIndexRange) => void;
	registerChild?: (ref: Table | null) => void;
	stopScrollPropagation: boolean;
	autoSizerProps: Object;
	tableProps: Object;
};
type State = {
	expandedRowsMap: object;
};
type ColumnProps = {
	dataKey: any;
	width: number;
};

export class CollapsibleTable extends React.Component<TableProps, State> {
	TableRef: Table | null = null;
	state = {
		expandedRowsMap: {}
	};

	componentDidMount() {
		const scroller = this.getScroller();
		if (scroller) {
			scroller.addEventListener('wheel', this._onScroll);
		}
	}

	componentWillUnmount() {
		const scroller = this.getScroller();
		if (scroller) {
			scroller.removeEventListener('wheel', this._onScroll);
		}
	}

	getScroller(): any {
		const { stopScrollPropagation } = this.props;
		if (this.TableRef && stopScrollPropagation) {
			const tableNode = ReactDOM.findDOMNode(this.TableRef);
			//	@ts-ignore
			return tableNode.querySelector('.ReactVirtualized__Table__Grid');
		} else {
			return null;
		}
	}

	_onScroll(event: any) {
		const { currentTarget, deltaY } = event;
		const { scrollTop, scrollHeight } = currentTarget;
		const targetHeight = getComputedStyle(currentTarget).height || '';
		if (
			(deltaY < 0 && scrollTop <= 0) ||
			(deltaY > 0 && scrollTop >= scrollHeight - parseInt(targetHeight))
		) {
			event.preventDefault();
		}
	}

	_onRowClick = (index: number) => {
		this.setState(
			{
				expandedRowsMap: this._getUpdatedExpandedRowsMap(index)
			},
			() => this._recomputeRowHeights(index)
		);
	};

	_rowHeight = (params: { index: number }) => {
		return this.props.rowHeight({
			index: params.index,
			expanded: this.state.expandedRowsMap[params.index]
		});
	};

	_getUpdatedExpandedRowsMap = (index: number): object => {
		return {
			...this.state.expandedRowsMap,
			[index]: this.state.expandedRowsMap[index] ? false : true
		};
	};

	_recomputeRowHeights = (index: number) => {
		if (this.TableRef) {
			this.TableRef.recomputeRowHeights(index);
			this.TableRef.forceUpdateGrid();
		}
	};

	_registerThyself = (ref: Table | null) => {
		this.TableRef = ref;
		if (this.props.registerChild) this.props.registerChild(ref);
	};

	_rowRenderer = (params: TableRowProps) => {
		const isLoaded = this.props.isRowLoaded
			? this.props.isRowLoaded({ index: params.index })
			: true;
		const _params = { ...params, isLoaded };

		return (
			isLoaded && (
				<CollapsibleRow
					showCaret={this.props.showCaret}
					caretElement={this.props.caretElement}
					index={params.index}
					style={params.style}
					className={this.props.rowClassName}
					header={this.props.rowRenderer(_params)}
					content={this.props.extraContentRenderer(_params)}
					expanded={this.state.expandedRowsMap[params.index]}
					onClick={this._onRowClick}
				/>
			)
		);
	};

	render() {
		const {
			data = [],
			// maxRowCount = 0,
			columns = [],
			height = 500,
			headerHeight = 50,
			onRowsRendered = ({  }: IndexRange & OverscanIndexRange) => {},
			noRowsRenderer = undefined,
			autoSizerProps = {},
			tableProps = {}
		} = this.props;

		return (
			<div className="table-header-styling" style={{ height }}>
				<AutoSizer {...autoSizerProps}>
					{({ width, height }) => (
						<Table
							{...tableProps}
							ref={this._registerThyself}
							width={width}
							height={height}
							headerHeight={headerHeight}
							rowCount={data.length}
							rowHeight={this._rowHeight}
							rowRenderer={this._rowRenderer}
							onRowsRendered={onRowsRendered}
							noRowsRenderer={noRowsRenderer}
							rowGetter={({ index }): any => data[index] || {}}>
							{columns.map(
								(props: ColumnProps, idx: number): React.ReactNode => (
									<Column {...props} key={idx} />
								)
							)}
						</Table>
					)}
				</AutoSizer>
			</div>
		);
	}
}

export const InfiniteCollapsibleTable = withInfiniteLoader(CollapsibleTable);
