import * as React from 'react';
import { InfiniteLoader, Index, IndexRange } from 'react-virtualized';

export type Props = {
	fetchData: (params: IndexRange) => Promise<any>;
	// dataRowCount?: number;
	// isArrowIconNeeded?: boolean;
	totalRowCount?: number;
	data: Array<any>;
	minimumBatchSize?: number;
	threshold?: number;
};
type State = {
	loadedRowsMap: object;
};

// const STATUS_LOADING = 1;
// const STATUS_LOADED = 2;

export default function withInfiniteLoader(
	WrappedComponent: typeof React.Component
): typeof React.Component {
	class InfinitelyLoadingComponent extends React.Component<Props, State> {
		state = {
			loadedRowsMap: {}
		};

		_isRowLoaded = ({ index }: Index) => {
			return !!this.props.data[index];
		};

		// _fetchData = (params: IndexRange) => {
		// 	const { loadedRowsMap } = this.state;
		// 	const { startIndex, stopIndex } = params;
		// 	const { fetchData = () => Promise.reject(null) } = this.props;

		// 	for (let i = startIndex; i <= stopIndex; i++) {
		// 		loadedRowsMap[i] = STATUS_LOADING;
		// 	}

		// 	this.setState({ loadedRowsMap });

		// 	return fetchData(params).then(response => {
		// 		for (let i = startIndex; i < response.length; i++) {
		// 			loadedRowsMap[i] = STATUS_LOADED;
		// 		}

		// 		this.setState({ loadedRowsMap });
		// 	});
		// };

		render() {
			const {
				// dataRowCount = undefined,
				// isArrowIconNeeded = true,
				minimumBatchSize = 10,
				threshold = 10,
				fetchData,
				totalRowCount = 10000
			} = this.props;

			return (
				<InfiniteLoader
					isRowLoaded={this._isRowLoaded}
					loadMoreRows={fetchData}
					rowCount={totalRowCount || 10000}
					minimumBatchSize={minimumBatchSize}
					threshold={threshold}>
					{({ onRowsRendered, registerChild }) => (
						<WrappedComponent
							{...this.props}
							isRowLoaded={this._isRowLoaded}
							// isArrowIconNeeded={isArrowIconNeeded}
							// maxRowCount={dataRowCount || 0}
							registerChild={registerChild}
							onRowsRendered={onRowsRendered}
						/>
					)}
				</InfiniteLoader>
			);
		}
	}

	return InfinitelyLoadingComponent;
}
